@import "sass/index";

.root {
  background: use(--template-gameCell-switch-text);
  &:hover {
    background: rgba(#ffffff, 0.2);
    box-shadow: -4px -4px 7px rgba(58, 68, 93, 0.4),
      4px 4px 7px rgba(6, 12, 24, 0.5);
  }
  text-transform: none;
  font-family: "Gilroy";
  font-size: 18px;
  font-weight: bold;
  color: use(--template-input-topLabel-color);
  border-radius: 8px;
  width: 40%;
  max-width: 300px;
  height: 40px;
  @media (max-width: 600px) {
    width: 100%;
  }
}
