@import "sass/index";

.optionsWrapper {
  background-color: use(--template-gameCell-controller);
  margin-top: 1px;
  padding: 10px;
  border-radius: 0 0 6px 6px;
  & > div {
    margin: 0;
  }
}

.statisticsWrapper {
  margin-top: 10px;
}

.gameWrapper {
  width: 100%;
  display: grid;
  grid-template-areas:
    "bet field"
    "rewards rewards";
  grid-template-columns: 1fr 1fr;
  background: var(--template-gameBlock-background);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border-radius: 6px 6px 0 0;
  @media (max-width: 1250px) {
    grid-template-areas:
      "field field"
      "rewards rewards"
      "bet bet";
  }
}

.bet {
  grid-area: bet;
}

.field {
  grid-area: field;
}

.rewards {
  grid-area: rewards;
}
