@import "sass/index";
@import "config/themes/_keno";

$mainIndent: 20px;

.betControl {
  margin-top: $mainIndent;
  & > div:not(:last-child) {
    margin-bottom: 10px;
  }
}

.buttonWrapper {
  text-align: center;
  margin-top: 20px;
}

.switch {
  justify-content: center;
  display: inline-flex;
  margin-right: $mainIndent;
  margin-bottom: $mainIndent;
  @media (max-width: 530px) {
    display: flex;
    margin-right: 0;
    margin-top: $mainIndent;
  }
}

.margin {
  margin-right: $mainIndent;
  @media (max-width: 600px) {
    margin-bottom: $mainIndent;
    margin-right: 0;
  }
}

.inputButtons {
  & button {
    background-color: $bossaNova;
  }
}

.refreshIcon {
  & svg > path {
    fill: $bossaNova;
  }
}
