@import "sass/index";

.root {
  padding-top: 20px;
  padding-left: 5%;
  padding-right: 5%;
  padding-bottom: 2%;
  background-color: use(--template-gameCell-controller);
  @media (max-width: 1250px) {
    padding-left: 1.5%;
    padding-right: 1.5%;
  }
}

.autoModeControl {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
  row-gap: 20px;
  @media (max-width: 550px) {
    grid-template-columns: 1fr;
    row-gap: 10px;
  }
}

.label {
  margin-bottom: 4px;
  color: use(--template-input-topLabel-color);
  font-family: "Gilroy";
  font-weight: bold;
}
