@import "sass/index";
@import "config/themes/_keno";

$fontFamily: "Varela Round";

.root {
  width: 100%;
  & > div {
    width: 100%;
  }
  & > h1 {
    margin: 0;
    font-family: "Gilroy";
    color: use(--template-input-topLabel-color);
    font-size: 14px;
    font-weight: bold;
    line-height: 17px;
    margin-bottom: 5px;
  }
}

.select {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  border: 1px solid $bossaNova;
  height: 45px;
  &:focus {
    border-radius: 8px;
  }
}

.selectLabel {
  & > span {
    color: use(--template-input-topLabel-color);
    font-family: $fontFamily;
    font-weight: 500;
    font-size: 18px;
    text-transform: capitalize;
  }
}

.icon {
  transform: scale(0.7);
  & polyline {
    stroke: use(--template-input-topLabel-color);
  }
}

.option {
  color: black;
  font-family: $fontFamily;
}
